<template>
  <div class="forget">
    <totalHead></totalHead>
    <div class="forget_center">
      <p class="log_deng">修改密码</p>
      <div class="forget_input">
        <Form ref="formCustom" :model="formCustom" :rules="ruleCustom">
          <FormItem prop="tel">
            <Input type="text" v-model.trim="formCustom.tel" placeholder="请输入手机号" style="width: 3rem;"></Input>
          </FormItem>
          <FormItem prop="passwd">
            <Input type="password" v-model.trim="formCustom.passwd" placeholder="请输入密码" style="width: 3rem;"></Input>
          </FormItem>
          <FormItem prop="passwdCheck">
            <Input type="password" v-model.tirm="formCustom.passwdCheck" placeholder="请再次输入密码" style="width: 3rem;"></Input>
          </FormItem>
          <div class="logg_roll">
            <input type="text" placeholder="验证码" v-model.tirm="code" /><button type="button" @click="getMessage" v-if="!shoe">{{msg}}</button><button type="button" v-if="shoe">{{time}}s重新获取</button>
          </div>
          <FormItem>
            <Button type="primary" @click="handleSubmit('formCustom')" style="width: 3rem;background: #96BD13;border: none;height: 0.4rem;">提交</Button>
          </FormItem>
        </Form>

      </div>

      <div class="logg_enro">
        <span @click="enter">修改成功？立即登录</span>
      </div>
    </div>
  </div>
</template>

<script>
   import totalHead from '../totalHead.vue'
  export default {
    data() {
      const validatePass = (rule, value, callback) => {
        if(value === '') {
          callback(new Error('请输入密码'));
        } else {
          if(this.formCustom.passwdCheck !== '') {
            // 对第二个密码框单独验证
            this.$refs.formCustom.validateField('passwdCheck');
          }
          callback();
        }
      };
      const validatePassCheck = (rule, value, callback) => {
        if(value === '') {
          callback(new Error('请再次输入密码'));
        } else if(value !== this.formCustom.passwd) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        time: 60,
        shoe: false,
        show:false,
        code:'',
        msg: "手机验证码",
        formCustom: {
          passwd: '',
          passwdCheck: '',
          tel: ''
        },
        ruleCustom: {
          passwd: [{
            validator: validatePass,
            trigger: 'blur'
          }],
          passwdCheck: [,
            {
              min: 6,
              max: 15,
              message: '密码的长度在6~15范围内',
              trigger: 'blur'
            }, {
              validator: validatePassCheck,
              trigger: 'blur'
            }
          ],
          tel: [{
            required: true,
            message: '手机号不能为空',
            trigger: 'blur'
          }, {
            pattern: /^1[34578]\d{9}$/,
            message: '请输入中国国内的手机号码',
            trigger: 'blur'
          }]

        },
      }
    },
    methods: {
      enter() {
        this.$router.go(-1);
      },
      getMessage() {
        if(this.tel !== "") {
          this.show = true;
          const timer = setInterval(() => {
            this.time--;
            if(this.time === 0) {
              this.msg = "重新获取验证码";
              this.show = false;
              this.time = 60;
              clearInterval(timer);
            }
          }, 1000);
          this.post('custom/First/sendMsg', {
            tel: this.formCustom.tel,
          }).then(res => {
            if(res.data.code === 10000) {
              this.$Modal.success({
                title: "发送成功",
                content: "您的手机号验证码已发送成功请注意查收"
              });
            } else {
              this.$Modal.error({
                title: "发送失败",
                content: "手机号发送失败，请重新发送"
              });
            }
          })
        } else {
          this.$Modal.error({
            title: "获取失败",
            content: "请输入手机号"
          });
        }
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if(valid) {
            this.post("custom/First/forget", {
              username: this.formCustom.tel,
              password: this.formCustom.passwd,
              code: this.code,
            }).then(res => {
              if(res.data.code === 10000) {
                this.$Modal.success({
                  title: "修改成功",
                  content: "密码修改成功"
                });
              }
            });
          } else {
            this.$Modal.error({
              title: "修改失败",
              content: "表单验证失败"
            });
          }
        })
      }
    },
    components: {
      totalHead
    },
    mounted() {
      if(this.$route.name === "forget") {
        this.$store.dispatch("fetch_load", false)
      }
    }
  }
</script>

<style>
  .forget {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #4D336F;
    overflow: auto;
    background: url(../../assets/login_backgroundImg.png) no-repeat;
    background-size:100% 100% ;
  }
  
  .forget_center {
    width: 4rem;
    background: white;
    margin: 0 auto;
    margin-top: 2rem;
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;
    box-shadow: 5px 5px 10px #1E87A3;
  }
  
  .logg_roll {
    margin-top: 0.1rem;
    margin-bottom: 0.24rem;
  }
  
  .logg_roll>button {
    width: 1rem;
    height: 0.35rem;
    color: white;
    border: none;
    background: #63A35C;
    border-top-right-radius: 0.04rem;
    border-bottom-right-radius: 0.04rem;
  }
  
  input::-moz-placeholder,
  input:-ms-input-placeholder,
  input::-webkit-input-placeholder {
    color: #666666;
  }
  
  .logg_roll>input {
    border-top-left-radius: 0.04rem;
    border-bottom-left-radius: 0.04rem;
    width: 1rem;
    height: 0.35rem;
    padding-left: 0.04rem;
  }
  
  .logg_enro {
    text-align: center;
    color: #2C2C2C;
    font-size:0.12rem;
  }
  
  .logg_enro>span {
    cursor: pointer;
  }
  .forget_input{
    margin: 0 auto;
  }
  .ivu-form-item {
    /*margin: 0 auto !important;*/
   margin-left: 0.5rem;
    margin-bottom: 0.18rem !important;
  }
  
  .ivu-input {
    height: 43px !important;
  }
</style>


